import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material';
import { AnalyticsDashboardService } from './analytics.service';
import { fuseAnimations } from '@fuse/animations';
import { ContactsFakeDb } from '../../../../../fuse-fake-db/contacts';
import { Subject } from 'rxjs';
import { startOfDay, isSameDay, isSameMonth } from 'date-fns';
import { CalendarEvent, CalendarEventAction, CalendarEventTimesChangedEvent, CalendarMonthViewDay } from 'angular-calendar';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { FuseEventFormDialogComponent } from './event-form/event-form.component';
import { CalendarEventModel } from '../../calendar/event.model';
import { CalendarService } from '../../calendar/calendar.service';
import { ToasterService } from '@fuse/services/toaster-service.service';
import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { AnalyticsDashService } from './analytics-dash.service';

@Component({
    selector     : 'fuse-analytics-dashboard',
    templateUrl  : './analytics.component.html',
    styleUrls    : ['./analytics.component.scss'],
    animations   : fuseAnimations
})
export class FuseAnalyticsDashboardComponent
{
    query: string;
    widgets: any;
    widget1SelectedYear = '2016';
    widget5SelectedDay = 'today';
    searchInput: FormControl;
    contacts: any = ContactsFakeDb.contacts;
    displayedColumns = [ 'avatar', 'name', 'email', 'phone'];
    dataSource: any;
    client: any;
    task: any;

    view: string;
    viewDate: Date;
    events: CalendarEvent[];
    public actions: CalendarEventAction[];
    activeDayIsOpen: boolean;
    refresh: Subject<any> = new Subject();
    dialogRef: any;
    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
    selectedDay: any;

    constructor(
        private analyticsDashboardService: AnalyticsDashboardService,
        public dialog: MatDialog,
        public calendarService: CalendarService,
        private apiloader: ApiLoaderService,
        private toast: ToasterService,
        private analyics: AnalyticsDashService
    )
    {
        // Get the widgets from the service
        this.widgets = this.analyticsDashboardService.widgets;
         //console.log(this.widgets);
        // Register the custom chart.js plugin
        this.registerCustomChartJSPlugin();

        this.view = 'month';
        this.viewDate = new Date();
        this.activeDayIsOpen = true;
        this.selectedDay = {date: startOfDay(new Date())};

        this.actions = [
            {
                label  : '<i class="material-icons s-16">edit</i>',
                onClick: ({event}: { event: CalendarEvent }): void => {
                    this.editEvent('edit', event);
                }
            },
            {
                label  : '<i class="material-icons s-16">delete</i>',
                onClick: ({event}: { event: CalendarEvent }): void => {
                    this.deleteEvent(event);
                }
            }
        ];

    }

    ngOnInit()
    {
        this.getAllUsers();
        this.getClientDetails();
        this.getHolidays();

        /**
         * Watch re-render-refresh for updating db
         */
        this.refresh.subscribe(updateDB => {
            // console.warn('REFRESH');
            if ( updateDB )
            {
                // console.warn('UPDATE DB');
                this.calendarService.updateEvents(this.events);
            }
        });

        this.calendarService.onEventsUpdated.subscribe(events => {
            this.setEvents();
            this.refresh.next();
        });
    }

    /**
     * Register a custom plugin
     */
    registerCustomChartJSPlugin()
    {
        (<any>window).Chart.plugins.register({
            afterDatasetsDraw: function (chart, easing) {
                // Only activate the plugin if it's made available
                // in the options
                if (
                    !chart.options.plugins.xLabelsOnTop ||
                    (chart.options.plugins.xLabelsOnTop && chart.options.plugins.xLabelsOnTop.active === false)
                )
                {
                    return;
                }

                // To only draw at the end of animation, check for easing === 1
                const ctx = chart.ctx;

                chart.data.datasets.forEach(function (dataset, i) {
                    const meta = chart.getDatasetMeta(i);
                    if ( !meta.hidden )
                    {
                        meta.data.forEach(function (element, index) {

                            // Draw the text in black, with the specified font
                            ctx.fillStyle = 'rgba(255, 255, 255, 0.7)';
                            const fontSize = 13;
                            const fontStyle = 'normal';
                            const fontFamily = 'Roboto, Helvetica Neue, Arial';
                            ctx.font = (<any>window).Chart.helpers.fontString(fontSize, fontStyle, fontFamily);

                            // Just naively convert to string for now
                            const dataString = dataset.data[index].toString() + 'k';

                            // Make sure alignment settings are correct
                            ctx.textAlign = 'center';
                            ctx.textBaseline = 'middle';
                            const padding = 15;
                            const startY = 24;
                            const position = element.tooltipPosition();
                            ctx.fillText(dataString, position.x, startY);

                            ctx.save();

                            ctx.beginPath();
                            ctx.setLineDash([5, 3]);
                            ctx.moveTo(position.x, startY + padding);
                            ctx.lineTo(position.x, position.y - padding);
                            ctx.strokeStyle = 'rgba(255,255,255,0.12)';
                            ctx.stroke();

                            ctx.restore();
                        });
                    }
                });
            }
        });
    }

    getAllUsers(){
        const id = sessionStorage.getItem('client_id');
        this.analyics.getData('users', { 'client_id': id } ).subscribe(response => {
            if (response.data){
                this.dataSource = response.data;
            }
        }, error => {
            this.toast.showError(error);
        });
    }

    getClientDetails(){
        this.apiloader.setLoader(true);
        const id = sessionStorage.getItem('client_id');
        this.analyics.getData('clients/' + id).subscribe(response => {
            if (response.data){
                this.apiloader.setLoader(false);
                this.client = response.data;
            }
        }, error => {
            this.apiloader.setLoader(false);
            this.toast.showError(error);
        });
    }

    getHolidays(){
        this.apiloader.setLoader(true);
        this.analyics.getData('holidays').subscribe(response => {
            if (response.data){
                this.apiloader.setLoader(false);
                 const events = response.data;
                 for (let i = 0; i < events.length; i++){
                    events[i].title = events[i].details;
                    events[i].start = new Date(events[i].date);
                    events[i].color = {
                        primary  : '#ad2121',
                        secondary: '#FAE3E3'
                    };
                    events[i].actions = [];
                    events[i].isHoliday = true;
                 }
                 this.events = events;
                 this.getTasks();
            }
        }, error => {
            this.apiloader.setLoader(false);
            this.toast.showError(error);
        });
    }

    setEvents()
    {
        this.events = this.events.map(item => {
            return new CalendarEventModel(item);
        });
    }

    /**
     * Before View Renderer
     * @param {any} header
     * @param {any} body
     */
    beforeMonthViewRender({header, body})
    {
        body.forEach((day) => {
            const event = day.events;
            if (event.length){
                event.forEach(item => {
                    if ( item.isHoliday ){
                        day.cssClass = 'holiday';
                    }
                });
            }
        });
    }

    getTasks(){
        let id = sessionStorage.getItem('user_id');
        this.apiloader.setLoader(true);
        this.analyics.getData('users/' + id).subscribe(response => {
            if (response.data){
                this.apiloader.setLoader(false);
                this.task = response.data.tasks;
                for (let i = 0; i < this.task.length; i++){
                    this.task[i].color = {
                        primary  : '#ad2121',
                        secondary: '#FAE3E3'
                    };
                    /* this.task[i].actions = this.actions; */
                    this.task[i].start = new Date(this.task[i].start);
                    this.events.push(this.task[i]);
                }
                this.setEvents();
            }
        }, error => {
            this.apiloader.setLoader(false);
            this.toast.showError(error);
        });
    }

    /**
     * Day clicked
     * @param {MonthViewDay} day
     */
    dayClicked(day: CalendarMonthViewDay): void
    {
        const date: Date = day.date;
        const events: CalendarEvent[] = day.events;

        if ( isSameMonth(date, this.viewDate) )
        {
            if ( (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) || events.length === 0 || day.cssClass == 'holiday' )
            {
                this.activeDayIsOpen = false;
            }
            else
            {
                this.activeDayIsOpen = true;
                this.viewDate = date;
            }
        }
        this.selectedDay = day;
        this.refresh.next();
    }

    /**
     * Event times changed
     * Event dropped or resized
     * @param {CalendarEvent} event
     * @param {Date} newStart
     * @param {Date} newEnd
     */
    eventTimesChanged({event, newStart, newEnd}: CalendarEventTimesChangedEvent): void
    {
        event.start = newStart;
        event.end = newEnd;
        // console.warn('Dropped or resized', event);
        this.refresh.next(true);
    }

    /**
     * Delete Event
     * @param event
     */
    deleteEvent(event)
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                const eventIndex = this.events.indexOf(event);
                this.events.splice(eventIndex, 1);
                this.refresh.next(true);
            }
            this.confirmDialogRef = null;
        });

    }

    /**
     * Edit Event
     * @param {string} action
     * @param {CalendarEvent} event
     */
    editEvent(action: string, event: CalendarEvent)
    {
        const eventIndex = this.events.indexOf(event);

        this.dialogRef = this.dialog.open(FuseEventFormDialogComponent, {
            panelClass: 'event-form-dialog',
            data      : {
                event : event,
                action: action
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }
                const actionType: string = response[0];
                const formData: FormGroup = response[1];
                switch ( actionType )
                {
                    /**
                     * Save
                     */
                    case 'save':

                        this.events[eventIndex] = Object.assign(this.events[eventIndex], formData.getRawValue());
                        this.refresh.next(true);

                        break;
                    /**
                     * Delete
                     */
                    case 'delete':

                        this.deleteEvent(event);

                        break;
                }
            });
    }

    /**
     * Add Event
     */
    addEvent(): void
    {
        this.dialogRef = this.dialog.open(FuseEventFormDialogComponent, {
            panelClass: 'event-form-dialog',
            data      : {
                action: 'new',
                date  : this.selectedDay.date
            }
        });
        this.dialogRef.afterClosed()
            .subscribe((response: FormGroup) => {
                if ( !response )
                {
                    return;
                }
                const newEvent = response.getRawValue();
                newEvent.actions = this.actions;
                this.events.push(newEvent);
                this.refresh.next(true);
            });
    }
}
