import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { MatButtonModule, MatDatepickerModule, MatInputModule,  MatTableModule, MatFormFieldModule, MatIconModule, MatMenuModule, MatSelectModule, MatTabsModule, MatSlideToggleModule, MatToolbarModule} from '@angular/material';

import { AgmCoreModule } from '@agm/core';
import { ChartsModule } from 'ng2-charts';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { ColorPickerModule } from 'ngx-color-picker';
import { FuseConfirmDialogModule } from '@fuse/components';
import { CalendarService } from '../../calendar/calendar.service';

import { CalendarModule} from 'angular-calendar';
import { FuseSharedModule } from '@fuse/shared.module';
import { FuseWidgetModule } from '@fuse/components/widget/widget.module';

import { CdkTableModule } from '@angular/cdk/table';
import { FuseAnalyticsDashboardComponent } from './analytics.component';
import { AnalyticsDashboardService } from './analytics.service';
import { FuseEventFormDialogComponent } from './event-form/event-form.component';
import { AnalyticsDashService } from './analytics-dash.service';


const routes: Routes = [
    {
        path     : '**',
        component: FuseAnalyticsDashboardComponent,
        resolve  : {
            data: AnalyticsDashboardService,
            chat : CalendarService
        }
    }
];

@NgModule({
    declarations: [
        FuseAnalyticsDashboardComponent,
        FuseEventFormDialogComponent
    ],
    imports     : [
        RouterModule.forChild(routes),
        
        MatButtonModule,
        MatFormFieldModule,
        MatIconModule,
        MatMenuModule,
        MatSelectModule,
        MatTabsModule,
        MatInputModule,
        CdkTableModule,
        MatTableModule,
        MatSlideToggleModule,
        MatToolbarModule,
        MatDatepickerModule,

        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyD81ecsCj4yYpcXSLFcYU97PvRsE_X8Bx8'
        }),
        ChartsModule,
        NgxChartsModule,
        CalendarModule.forRoot(),
        FuseSharedModule,
        FuseWidgetModule,
        ColorPickerModule,
        FuseConfirmDialogModule
    ],
    providers   : [
        AnalyticsDashboardService,
        CalendarService,
        AnalyticsDashService
    ],
    entryComponents: [FuseEventFormDialogComponent]
})
export class FuseAnalyticsDashboardModule
{
}

